<template>
    <div id="footer">
     {{ msg}}
    </div>
  </template>
  
  
  <style>
    #footer {
      position: fixed;
      bottom: 0px;
      z-index: 100;
      width: 100%;
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      background: #286f73;
      color: #fff;
    }
  </style>

  <script>
  export default {
    name: "footerBar",
    props:["toFooterMsg"],
    data(){
      return {
        msg: ""
      }
    },
    methods:{
      currentTime() {
          var date = new Date();
          var dateArr = [
              date.getHours(),
              date.getMinutes(),
              date.getSeconds(),
          ];
          //如果格式是MM则需要此步骤，如果是M格式则此循环注释掉
          for (var i = 0; i < dateArr.length; i++) {
              if (dateArr[i] >= 1 && dateArr[i] <= 9) {
                  dateArr[i] = "0" + dateArr[i];
              }
          }
          var strDate =
              dateArr[0] +
              ":" +
              dateArr[1] +
              ":" +
              dateArr[2];
              //此处可以拿外部的变量接收  strDate:2022-05-01 13:25:30
          return strDate;
      }
    },
    watch: {
      toFooterMsg: {
        handler: function(newVal){
          this.msg = this.currentTime() + " " + newVal;
        }
      }
    }

  }
  </script>