let toUnityUrl = "http://192.168.0.1:18002/ui/asyncdo";
let toServeUrl = "http://192.168.0.20:18102/web/asyncdo/";

let machineID = 9999;  //设备id

//封装数据，并将数据转为Json字符串格式
let toPackJsonStr = function(userName, method, data){
    var args = {
        Key: 0,
        token: "",
        userName: userName,
        type: "IAttributeExecute",
        methodName: method,
        methodArgs: data,
        ClientIP: null,
        Args: data
    };
    return JSON.stringify(args);
}

export default { 
    toUnityUrl,
    toServeUrl,
    machineID,
    toPackJsonStr
}
