<template>
    <!-- 右侧参数管理 -->
    <div v-if="curSelectWebApi != null" id="paramsMgr">
        <el-row class="controller">
            <h2 class="explain">{{curSelectWebApi != null ? (this.curSelectWebApi.webApi.des + this.curSelectWebApi.webApi.name) : "" }}</h2>
            <el-button type="primary">锁定</el-button>
            <el-button type="success">保存</el-button>
        </el-row>
        <el-divider></el-divider>
        <div class="content">
             <!-- 参数区域 -->
            <div>
                <div class="item" v-for="(item, index) in paramData" :key="index">
                    <div v-if="values">
                        <el-input :placeholder="'请输入' + item.name" v-model="values[index].paramValue">
                            <template slot="prepend">{{ item.name }}</template>
                        </el-input>
                        <h4 class="info" v-if="item.isCanValue">
                            <span v-for="(value, index) in item.values" :key="index">{{value.canValue + value.des}}</span>
                        </h4>
                    </div>
                </div>
            </div>
            <el-row v-if="itemType == 'out'">
                <el-button type="primary" @click="toUnityResquest">To Unity</el-button>
                <div class="res">
                    <h4>Unity响应的数据：</h4>
                    <div class="text-wrapper">{{resInfo}}</div>
                </div>
            </el-row>
            <el-row v-if="itemType == 'in'">
                <el-button type="primary" @click="onWriteServe">Write Serve</el-button>
                <div class="res response">
                    <h4>保存在Serve缓冲中：响应Unity请求的值</h4>
                    <div class="text-wrapper">{{unityResponseInfo}}</div>
                </div>
                <div class="res request">
                    <h4>Unity请求的数据：</h4>
                    <div class="text-wrapper">{{unityResInfo}}</div>
                </div>
            </el-row>
        </div>
    </div>
</template>

<style>
#paramsMgr .controller{
    text-align: right;
    width: 100%;
    margin-top: 4px;
    height: 36px;
}
#paramsMgr .controller .explain{
    display: inline-block;
    margin-right: 60px;
}
#paramsMgr .el-divider{
    margin: 4px 0;
}
#paramsMgr .controller button{
    display: inline-block;
    height: 100%;
    border: none;
}
.content .item {
    clear:both;
    padding: 6px;
}
.content .item::after{
    content: "";
    clear: both;
    display: block;
}
.content .item .el-input{
    float: left;
    width: 300px;
}
.content .item .el-input .el-input-group__prepend{ 
    width: 100px;
}
.content .item .info{
    display: inline-block;
    float: left;
    padding: 10px;
}
.content .item .info span{
    display: inline-block;
    padding-left: 10px;
}
.content .el-row{
    margin-top: 10px;
}
.content .el-button{
    width: 100%;
}
.content .el-row .res{
    width: 100%;
    margin-top: 10px;
    padding: 10px 0px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    text-indent: 10px;
}
.content .el-row .response{
    background-color: #fffecb;
}
.content .el-row .request{
    background-color: #ffd2cb;
}
.text-wrapper {
  white-space: pre-wrap;
}
</style>
<script>
export default {
    name:"paramsMgr",
    props:['curSelectWebApi', 'curMachine', "webSocketMsg", 'isClickWebApiBtn'],
    beforeDestroy(){
    },
    /****
     * 
     * 数据域
     * 
     * **/
    data(){
        return {
            machineId: "未选择专机",
            methodName: "",
            paramData: null,                //解析后的参数
            values: [],                      //设定的值，包含类型 == 与toSendUnity中的data对应
            itemType: "",                    //接口类型（in out）
            resInfo: "",                     //Unity向应的数据
            unityResInfo: "",                 //Unity请求的数据
            unityResponseInfo: ""                 //Unity请求的数据
            
        }
    },
    /****
     * 
     * 方法域
     * 
     * **/
    methods:{
        /************************************************ 向Unity发送请求 *************************************************/
        toUnityResquest(){
            if(this.machineId == 0 || this.machineName == "暂无专机")
            {
                this.$message.error('当前未选择专机，禁止发送消息');
                return;
            }
            // 封装数据
            let data = {};          //传递给Unity的数据键值对，不包含类型
            for(var i = 0; i < this.values.length; i++){
                data[this.values[i].paramName] = this.values[i].paramValue;
                if(this.values[i].paramType == "string"){data[this.values[i].paramName]= "" + data[this.values[i].paramName];}
                else if(this.values[i].paramType == "int"){data[this.values[i].paramName] = Number(data[this.values[i].paramName]);}
                if(this.values[i].paramType == "bool"){data[this.values[i].paramName] = Boolean(data[this.values[i].paramName]);}
            }
            this.toSendUnity(data);
        },
         //发送请求
        async toSendUnity(data){
            var params = this.nethelp.toPackJsonStr("Unity3D", this.curSelectWebApi.webApi.name, data);
            await this.axios.post(this.nethelp.toUnityUrl, params).then((response) => {
                var res = JSON.stringify(response.data);
                if(response.data.success == true){
                    this.$message({
                        message: 'To Unity：消息发送成功\n' + this.methodName,
                        type: 'success'
                    });
                    this.resInfo = res.replace(/,/g, ',\n');
                    this.resInfo = this.resInfo.replace(/{/g, '{\n');
                    this.resInfo = this.resInfo.replace(/}/g, '}\n');
                }
                else {
                    this.$message.error(`url:${this.nethelp.toUnityUrl }\n    methodName:${this.curSelectWebApi.webApi.name}\n    res:${res}`);
                }
                
            }).catch((error) => {
                this.resInfo = `url:${this.nethelp.toUnityUrl }\n    methodName:${this.curSelectWebApi.webApi.name}\n    error:${error}`;
                this.$message.error(error);
                
            });
            
        },
        /************************************************************* 将响应信息写入到服务器 ********************************************/
        async onWriteServe(){
            if(this.machineId == 0 || this.machineName == "暂无专机")
            {
                this.$message.error('当前未选择专机，禁止发送消息');
                return;
            }
           var params = this.nethelp.toPackJsonStr("Web", "Web.Machine.WriteInData", { methodName: this.methodName, data: this.values});
            await this.axios.post(this.nethelp.toServeUrl, params, {
                headers: {
                "Content-Type": "application/json",
                'Access-Control-Allow-Origin':'*'
                }
            }).then((response) => {
                this.$message({
                    message: 'To Unity：消息发送成功\n' + this.methodName,
                    type: 'success'
                });
                this.resInfo = response.data;
            }).catch((error) => {
                alert(error);
            });

            this.queryInData();
        },
         /************************************************************* 查询缓存数据 ********************************************/
         async queryInData(){
            if(this.machineId == 0 || this.machineName == "暂无专机")
            {
                this.$message.error('当前未选择专机，禁止发送消息');
                return;
            }
           var params = this.nethelp.toPackJsonStr("Web", "Web.Machine.QueryCaches", { methodName: this.methodName});
            await this.axios.post(this.nethelp.toServeUrl, params, {
                headers: {
                "Content-Type": "application/json",
                'Access-Control-Allow-Origin':'*'
                }
            }).then((response) => {
                var res = response.data.result;
                if(res == null) return;
                if(res.itemValue == null) return;
                var str = "{\n";
                for(var i = 0; i < res.itemValue.length; i++){
                    str += `    ${res.itemValue[i].key}: ${res.itemValue[i].value}\n`;
                }
                str += "}";
                this.unityResponseInfo = str;
            }).catch((error) => {
                alert(error);
            });
        }
    },
    /****
     * 
     * 属性监视
     * 
     * **/
    watch:{
         /************************************ 将当前选择的接口配置信息进行解析已经设置默认值 ***************************************/
        curSelectWebApi :{
            handler: function(newVal, oldVal) {
                console.log(newVal, oldVal)
                if(newVal != null)
                {
                    this.values = [];
                    console.log(newVal);
                    this.itemType = newVal.webApi.type;
                    this.methodName = newVal.webApi.name;
                    //解析值
                    if(newVal == null || newVal == undefined) return;
                    let webApis = newVal.webApi;

                    let webApiData = [];
                    let paramNames = webApis.paramsName.indexOf(' ') == -1 ? webApis.paramsName :  webApis.paramsName.split(' ');
                    let paramTypes = webApis.paramsType.indexOf(' ') == -1 ? webApis.paramsType :  webApis.paramsType.split(' ');
                    let mayValues = webApis.mayBeValues.mayBeValue;
                    for(var i = 0; i < paramNames.length; i++)
                    {
                        let param = {};
                        param["name"] = paramNames[i];
                        param["type"] = paramTypes[i];
                        param["isCanValue"] = false;
                        for(var j = 0; j < mayValues.length; j++){
                            if(mayValues[j]["name"] == paramNames[i]){
                                param["isCanValue"] = true;
                                param["values"] = [];
                                let mayValueDes = mayValues[j].des.indexOf(' ') == -1 ? mayValues[j].des :  mayValues[j].des.split(' ');
                                let mayValueValue = mayValues[j].value.indexOf(' ') == -1 ? mayValues[j].value :  mayValues[j].value.split(' ');
                                for(var z = 0; z <  mayValueValue.length; z++){
                                    let value = {};
                                    value["canValue"] = mayValueValue[z];
                                    value["des"] = mayValueDes[z];
                                    param["values"].push(value);
                                }
                            }
                        }
                        webApiData.push(param);
                        //默认值设定
                        let defaultValue = {
                            paramName: param["name"],
                            paramType: param["type"],
                            paramValue: null
                        };
                        if(param["type"] == "string") defaultValue["paramValue"] = "";
                        else if(param["type"] == "int") defaultValue["paramValue"] = 0;
                        else if(param["type"] == "bool") defaultValue["paramValue"] = "false";

                        if(defaultValue["paramName"] == "ID") defaultValue["paramValue"] = this.machineId;
                        this.values.push(defaultValue);  
                    }
                    this.paramData = webApiData;
                    this.$forceUpdate();        //强制刷新页面
                }
            }
        },
         /********* 当前选择的专机名以及专机ID分发给当前选择的接口，并充当为ID参数的默认值 ************/
        curMachine:{
            handler: function(newVal){
                if(this.curMachine == null) {
                    this.machineId = "未选择专机";
                }
                else{
                    this.machineId = newVal.machineId
                }
                for(var i = 0; i < this.values.length; i++){
                    if(this.values[i].paramName == "ID") 
                    {
                        this.values[i].paramValue = this.machineId;
                    }
                    
                }
            }
        },
        /********* Unity 发送来的请求信息，并找到当前的接口的数据信息(解析) ************/
        receiveMsg: {
            handler: function(newVal){
                var that = this;
                if(that.curSelectWebApi == null) return;         //没有选择任何接口
                if(newVal != null){
                    console.log(newVal);
                    for(var i = 0; i < newVal.length; i++){
                        console.log(newVal[i].name + "  " +   that.methodName)
                        if(newVal[i].name == that.methodName){
                            that.unityResInfo = newVal[i].msgs[newVal[i].msgs.length - 1];
                            break;
                        }
                    }
                    console.log(that.unityResInfo);
                }
            },
            deep: true
        },
         /********* Unity 点击了侧边栏按钮 ************/
        isClickWebApiBtn: {
            handler: function(newVal){
                if(newVal == true){
                    var that = this;
                    //将WebSocket传递的数据进行解析
                    for(var i = 0; i < that.webSocketMsg.length; i++){
                        if(that.webSocketMsg[i].name == that.methodName){
                            that.unityResInfo = that.webSocketMsg[i].msg;    
                            break;
                        }
                    }
                    this.queryInData();
                }
                this.$emit("clickWebApiBtnHandleFunc", false);
                
            }
        }
    }
}
</script>