<template>
    <!-- 左侧按钮组 -->
    <div>
        <ul id="methodBar_tac"  v-if="webApiConfig != null">
            <li class="btn-item" v-for="(item, index) of webApiConfig.methods" :key="index" >
                <el-button @click="selectWebApi(item, index)" v-if="item.type == 'out'" class="btn_style">
                    <i class="el-icon-connection"></i>
                    <span>{{item.des}}</span>
                </el-button>
                <el-button class="haveIcon" style="background: #ffffff" @click="selectWebApi(item, index)" v-if="item.type == 'in'">
                    <i class="el-icon-connection"></i>
                    <span>{{item.des}}</span>
                    <span class="num" v-text="msgNum[item.name]" v-if="msgNum[item.name] > 0"></span>
                </el-button>
            </li>
        </ul>
    </div>
</template>

<style>
    #methodBar_tac li.btn-item{
        padding: 2px 10px;
    }
    #methodBar_tac li.btn-item button{
        display: block;
        width: 100%;
        height: 34px;
        text-align: left;
        padding-left: 10px;
    }
    li.btn-item button.btn_style{
        background-color: #286f73;
        color: #fff;
        border: none;
    }
    li.btn-item button.haveIcon{
        position: relative;
    }
    li.btn-item button .num{
        position: absolute;
        left: 167px;
        top: -8px;
        display: inline-block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background-color: red;
        color: #fff;
        text-align: center;
        line-height: 14px;
        font-size: 10px;
    }
</style>


<script>
export default {
    name:"sidebarBtnGroup",
    props:['webApiConfig', 'receiveMsg'],
    data(){
        return {
            curIndex: -1,
            msgNum: {}
        }
    },
    methods:{
        selectWebApi(webApi, index){
            /**********************************************************************************
             * webApi结构
             * des: "RGV申请取卸货"
             * mayBeValues: Object
             *      mayBeValue: Array(1)
             *          0: 
             *              des: "倒药工位卸货 倒药工位取货 缓存工位卸货 缓存工位取货"
             *              name: "PCCMD"
             *              value: "9 10 11 12"
             * name: "UCP.Packer.RgvApplyPickAndUnload"
             * paramsName: "ID PCCMD"
             * paramsType: "string int"
             * type: "out"
             * ***********************************************************************************/
            this.curIndex = -1;
            this.$emit("selectWebApiFunc", {webApi: webApi, curIndex: index});          //当前设备的WebApi传递出去
            this.$emit("clickWebApiBtnFunc", true);                                     //点击了按钮
            var methodName = webApi.name;
            this.msgNum[methodName] = 0;
            this.$forceUpdate();                //强制刷新页面
        }
    },
    watch:{
        webApiConfig: {
            handler: function(newVal){
                if(newVal != null){
                    for(var i = 0; i < this.webApiConfig.methods.length; i++){
                        this.msgNum[this.webApiConfig.methods[i].name] = 0;
                    }
                }
            }
        },
        receiveMsg: {
            handler: function(newVal){
                console.log(newVal + "dwdwa");
                if(newVal != null){
                    for(var i = 0; i < newVal.length; i++){
                        this.$set(this.msgNum, newVal[i].name, newVal[i].msgs.length)
                    }
                    this.$forceUpdate();        //强制刷新页面
                }
            },
            deep: true
        }
    }
}
</script>

