<template>
  <div id="top">
    <div class="content">
      <h1 @click="appSetter">LCSVisual</h1>
      <!-- <span class="URL_Style">TO UNITY：{{this.nethelp.toUnityUrl}}</span> -->
      <ul  v-if="machines != null">
        <li>
          <span class="span_text">专机：</span>
          <span class="span_text">{{machineName}}</span>
        </li>
        <li>
          <span class="span_text">编号：</span>
          <span class="span_text">
            <el-dropdown trigger="click" class="dropDown_machineId" @command="handleCommand">
              <span class="el-dropdown-link">
                {{machineId}}
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="(item, index) in machines" :key="index" :command="item">{{item.machineId + " " + item.machineName + " " + item.machineVersion}}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </span>
        </li>
        <li>
          <span class="span_text">版本：</span>
          <span class="span_text">{{machineVersion}}</span>
        </li>
        <li class="other">
          <el-button type="primary" @click="loadWebApiConfig">加载配置表</el-button>
        </li>
        <li class="other">
          <el-button type="primary" @click="reconnectWs">重新连接</el-button>
        </li>
      </ul>
    </div>
    <!-- 抽屉 -->
    <el-drawer title="draw" :visible.sync="drawer" :with-header="false" class="setter">
      <div class="drawer-content">
        <h1 class="el-icon-s-tools">设置</h1>
        <el-divider></el-divider>
        <div class="input-item">
          <p class="input-item-elem">Server URL：</p>
          <el-input v-model="serverUrl" placeholder="请输入内容" class="input-item-ele" :disabled="isDisabled" @blur="onBlur"></el-input>
        </div>
        <div class="input-item">
          <p class="input-item-elem">Unity URL：</p>
          <el-input v-model="unityUrl" placeholder="请输入内容" class="input-item-ele" :disabled="isDisabled"  @blur="onBlur"></el-input>
        </div>
        <div class="control-btns">
          <el-row>
            <el-button type="primary" round @click="isDisabled = !isDisabled" style="width: 100%;">{{isDisabled == true ? '修改' : '禁用'}}</el-button>
          </el-row>
          <el-row>
            <el-button type="primary" round @click="loadMachine" style="width: 100%;">重新加专机</el-button>
          </el-row>
          <el-row>
            <el-button type="warning" round @click="ClearCaches" style="width: 100%;">清空缓存</el-button>
          </el-row>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<style>
  #top{
    width: 100%;
    height: 36px;
    background: #286f73;
  }
  #top .content{
    width: 1000px;
    height: 100%;
    margin: 0 auto;
  }
  #top .content .URL_Style{
    display: inline-block;
    height: 100%;
    line-height: 36px;
    color: #fff;
  }
  .content h1{
    float: left;
    width: 100px;
    height: 100%;
    line-height: 36px;
    color: #fff;
    cursor: pointer;
  }
  .content ul{
    float: right;
    height: 100%;
  }
  .content ul li{
    float: left;
    height: 100%;
    padding-left: 14px;
    /* padding-right: 14px; */
    color: #ffffff;
  }
  .content ul li span.span_text{
    display: inline-block;
    height: 100%;
    line-height: 36px;
    padding-left: 4px
  }
  .content ul li .dropDown_machineId{
    color: #fff;
    font-size: 12px;
    cursor: pointer;
  }
  .content ul li.other button{
    width: 100px;
    height: 24px;
    font-size: 12px;
    line-height: 0px;
    margin: 6px 0;
    border: none;
  }
  #top .setter .drawer-content{
    padding: 10px;
  }
  .setter .drawer-content h1{
    font-weight: bold;
  }
  .setter .drawer-content .input-item .input-item-elem{
    float: left;
  }
  .setter .drawer-content .input-item p{
    width: 80px;
    height: 40px;
    line-height: 40px;
    font-weight: bold;
  }
  .setter .drawer-content .input-item .el-input{
    width: 355px;
    height: 100%;
    line-height: 40px;
  }
  .setter .drawer-content .input-item .el-input input{
    border-radius: 15px;
    height: 30px;
  }
  .setter .drawer-content .control-btns{
    width: 100%;
    margin-top: 20px;
  }
  .setter .drawer-content .control-btns .el-row{
    margin-top: 10px;
  }
</style>
<script>
export default {
    name:"topConfig",
    props:['machines'],
    data(){
        return {
          drawer: false,                //控制抽屉显示隐藏
          isDisabled: false,
          machineName: "暂无专机",
          machineId: 0,
          machineVersion: 0.0,
          serverUrl: "http://192.168.0.20:18102/web/asyncdo",
          unityUrl:"http://192.168.0.1:18002/ui/asyncdo/"
        }
    },
    methods: {
      /****************************  设置  ************************** */
      appSetter(){
        this.drawer = true;
      },
      /****************************  专机  ************************** */
      async loadMachine(){
        var machineArr = [];
        var params = this.nethelp.toPackJsonStr("Web", "Web.Machine.LoadMachineInfo", { });
        await this.axios.post(this.nethelp.toServeUrl, params, {
            headers: {
            "Content-Type": "application/json",
            'Access-Control-Allow-Origin':'*'
            }
        }).then((response) => {
            if(response.data.success == false){
              this.$message({
                showClose: true,
                message: response.data.msg,
                type: 'error',
                duration: 0
              });
            }
            else
            {
              let machines = response.data.result.machines;
              for(var i = 0; i < machines.length; i++){
                let machine = {
                  machineId: machines[i]["id"],
                  machineName: machines[i]["name"],
                  machineVersion: machines[i]["version"]
                };
                machineArr.push(machine);
              }
              this.$emit("loadMachineDataFunc", machineArr);
            }
        }).catch((error) => {
          this.$message({
            showClose: true,
            message: this.nethelp.toServeUrl + "\n" +  "Web.Machine.LoadMachineInfo\n" + "Error：" + error,
            type: 'error',
            duration: 0
          });
        });
      },
      handleCommand(command){
        this.machineId = command.machineId;
        this.machineName = command.machineName;
        this.machineVersion = command.machineVersion;
      },
       /****************************  请求WebApi配置文件  ************************** */
      async loadWebApiConfig(){
          if(this.machineName == "暂无专机" || this.machineId == 0) {
            this.$message.error('加载WebApi配置失败，请先选择专机！');
            return;
          }
          var params = this.nethelp.toPackJsonStr("Web", "Web.Machine.LoadWebApiConfig", {machineId: this.machineId, machineName: this.machineName, machineVersion: this.machineVersion});
          await this.axios.post(this.nethelp.toServeUrl, params, {
              headers: {
              "Content-Type": "application/json",
              'Access-Control-Allow-Origin':'*'
              }
          }).then((response) => {
              if(response.data.success == false){ alert(response.data.msg); }
              else { this.$emit("webApiConfigFunc", response.data.result); }
          }).catch((error) => {
            this.$message.error("加载专机接口配置：" + error);
          });
      },
      /****************************  清空数据缓存  ************************** */
      async ClearCaches(){
          var params = this.nethelp.toPackJsonStr("Web", "Web.Machine.ClearCaches", {});
          await this.axios.post(this.nethelp.toServeUrl, params, {
              headers: {
              "Content-Type": "application/json",
              'Access-Control-Allow-Origin':'*'
              }
          }).then((response) => {
              if(response.data.success == false){ 
                this.$message.error("清空数据缓存" + response.data);
              }
              else
              {
                this.$message({
                  message: response.data.result,
                  type: 'success'
                });
                this.machineId = 0;
                this.machineName = "暂无专机";
              }
          }).catch((error) => {
            this.$message.error("加载专机接口配置：" + error);
          });
      },
      reconnectWs(){
        this.$emit("reconnectWSFunc");
      },
      onBlur(){
        this.nethelp.toServeUrl = this.serverUrl;
        this.nethelp.toUnityUrl = this.unityUrl;
        console.log(this.nethelp.toServeUrl);
        console.log(this.nethelp.toUnityUrl);
      }
    },
    watch:{
      machineId:{
        handler: function(){
          this.$emit("selectMachineFunc", {machineId: this.machineId, machineName: this.machineName});
        }
      },
      machines: {
        handler: function(newVal){
          console.log(newVal);
        },
        deep: true
      },
      /********* URL 监视 **************/
      serverUrl: {
        handler: function(newVal){
          this.nethelp.toServeUrl = newVal;
        }
      },
      unityUrl: {
        handler: function(newVal){
          this.nethelp.toServeUrl = newVal;
        }
      }
    }

}
</script>
