import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import nethelp from "./js/nethelp"
import '@/style/reset.css'


Vue.config.productionTip = false

//全局变量
Vue.prototype.axios = axios;
Vue.prototype.nethelp = nethelp;
Vue.use(ElementUI);

//与设备相关的
Vue.prototype.$_machineId = "";
Vue.prototype.$_machineName = "";

//与webSocket相关
Vue.prototype.WEB_SOCKET;
Vue.prototype.webSocketUrl = 'ws://192.168.0.20:18102'; //如果开启了https则这里是wss
Vue.prototype.receiveFromUnityMsg = "";

new Vue({
  render: h => h(App),
}).$mount('#app')

